import request from '@/plugin/axios'

/**
 *  Api
 * https://github.com/jokermonn/-Api/blob/master/OneArticle.md
 * @param param
 */

// 
export function getArticleDate ({param}) {
  return request({
    url: `https://interface.meiriyiwen.com/article/day?dev=1&date=${param.date}`,
    method: 'get'
  })
}
// 
export function getArticleToday () {
  return request({
    url: `https://interface.meiriyiwen.com/article/today?dev=1`,
    method: 'get'
  })
}
//
export function getArticleRandom () {
  return request({
    url: `https://interface.meiriyiwen.com/article/random?dev=1`,
    method: 'get'
  })
}
