<template>
  <div id="app">
    <router-view></router-view>
  </div>
</template>
<script>
export default {
  name: 'app',
  data () {
    return {
      clickCount: 0
    }
  },
  mounted () {

  },
  methods: {
    vueClick (e) {// 
      /**
       * dom
       * dom
       * dom
       * @author:slzs
       */
      let symbol = document.createElement('b')
      symbol.style.position = 'absolute'
      symbol.style.left = (e.pageX - 10) + 'px'
      symbol.style.top = (e.pageY - 15) + 'px'
      symbol.style.zIndex = '9999'
      symbol.style.userSelect = 'none' // 
      symbol.style.fontSize = '18px'
      symbol.style.color = `rgb(${Math.floor(Math.random() * 256)},${Math.floor(Math.random() * 256)},${Math.floor(Math.random() * 256)})` // 
      symbol.style.transition = 'all 1.5s'
      symbol.addEventListener('transitionend', function (et) { // dom
        if (et.propertyName === 'opacity' && et.srcElement.style.opacity === 0)
          et.srcElement.remove()
      })
      // 
      /**
       * 
       * 
       * 
       * 
       * 
       * 
       * 
       * 
       * 
       * 
       * 
       * 
       * 
       * 
       * 
       */
      switch (++this.clickCount) {
        case 1:
          symbol.innerText = ''
          break
        case 2:
          symbol.innerText = ''
          break
        case 3:
          symbol.innerText = ''
          break
        case 4:
          symbol.innerText = ''
          break
        case 5:
          symbol.innerText = ''
          break
        case 6:
          symbol.innerText = ''
          break
        case 7:
          symbol.innerText = ''
          break
        case 8:
          symbol.innerText = ''
          break
        case 9:
          symbol.innerText = ''
          break
        case 10:
          symbol.innerText = ''
          break
        case 11:
          symbol.innerText = ''
          break
        case 12:
          symbol.innerText = ''
          break
        default:
          symbol.innerText = ''
          this.clickCount = 0
      }
      document.body.appendChild(symbol)
      requestAnimationFrame(() => {
        symbol.style.top = (e.pageY - 100) + 'px'
        symbol.style.opacity = 0
      })
    },
  }
}
</script>

<style lang='scss'>
@import '~@/assets/style/public-class.scss';

#app {
  font-family: 'PingFangSc', 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(0deg, #ffffff, #e6f1fd);
}
._v-container {
  position: static !important;
}
html, body {
  /*cursor: url("~/img/cursor.jpg"), auto;*/
  // 
}
@media screen and (max-device-width: 400px) {
  // 400px (web )
  ._v-container {
    overflow-y: hidden !important;
  }
}
</style>
