import axios from 'axios'
import { Message } from 'element-ui'

// 
function errorLog (err) {
  // 
  console.log(err)
  // 
  Message({
    message: err.message,
    type: 'error',
    duration: 5 * 1000
  })
}

//  axios 
const service = axios.create({
  // baseURL: process.env.VUE_APP_API, // 
  timeout: 5000 // 
})

//  
// if (process.env.VUE_APP_BUILD_MODE === 'DEV') {
//   // 
//   service.defaults.baseURL = 'https://dev.hjhx.cn/api'
// } else if (process.env.VUE_APP_BUILD_MODE === 'TEST') {
//   // 
//   service.defaults.baseURL = 'https://dev.hjhx.cn/api'
// } else if (process.env.VUE_APP_BUILD_MODE === 'PRO') {
//   // 
//   service.defaults.baseURL = 'https://api.hjhx.cn/'
// } else {
//   service.defaults.baseURL = 'https://dev.hjhx.cn/api'
// }
//
// console.log('service.defaults.baseURL', service.defaults.baseURL)

// 
service.interceptors.request.use(
  config => {
    // 
    // config.headers = {
    //   'Content-Type': 'application/json',
    //   "Access-Control-Allow-Origin": "*"
    // }
    return config
  },
  error => {
    // 
    console.log(error)
    Promise.reject(error)
  }
)

// 
service.interceptors.response.use(
  response => {
    // dataAxios  axios  data
    const dataAxios = response.data
    //   
    return dataAxios
  },
  error => {
    if (error && error.response) {
      switch (error.response.status) {
        case 400:
          error.message = ',  API '
          break
        case 401:
          error.message = ''
          break
        case 403:
          error.message = ''
          break
        case 404:
          error.message = `: ${error.response.config.url}`
          break
        case 408:
          error.message = ''
          break
        case 500:
          error.message = ''
          break
        case 501:
          error.message = ''
          break
        case 502:
          error.message = ''
          break
        case 503:
          error.message = ''
          break
        case 504:
          error.message = ''
          break
        case 505:
          error.message = 'HTTP'
          break
        default:
          break
      }
    } else {
      if (error.toString().indexOf('timeout') !== -1) {
        error.message = ''
      } else {
        error.message = ''
      }
    }
    errorLog(error)
    return Promise.reject(error)
  }
)

export default service
